<template>
  <div class="userprint">
    <div>
      <v-card-text class="mt-3">
        <!-- 畫面顯示內容區域 -->
        <h3>{{cData.company_name}}</h3>

        <div class="text-center border-bottom my-2">
          <h1>
            <strong>住民基本資料表</strong>
          </h1>
        </div>
        <hr />
        <div class="row">
          <div class="col-12 font-weight-bold">
            <v-form ref="form">
              <v-alert color="primary" border="left" outlined>
                <v-sheet color="blue dark-1" class="pa-2 white--text" elevation="4">
                  <h2>個人基本資料</h2>
                </v-sheet>
                <table width="100%">
                  <tr>
                    <td>
                      <v-row>
                        <v-col class="py-0 my-0" cols="12" sm="4">
                          <v-text-field hide-details label="病歷號" v-model="list.p_code" ref="p_code"></v-text-field>
                        </v-col>
                        <v-col class="py-0 my-0" cols="12" sm="4">
                          <v-text-field hide-details label="姓名" v-model="list.name"></v-text-field>
                        </v-col>
                        <v-col class="py-0 my-0" cols="12" sm="4">
                          <v-text-field hide-details label="性別" v-model="list.sex"></v-text-field>
                        </v-col>
                        <v-col class="py-0 my-0" cols="12" sm="2">
                          <v-checkbox
                            class="py-0 my-0"
                            label="公費資格"
                            v-model="list.publicexpenses"
                            :value="true"
                          ></v-checkbox>
                        </v-col>
                        <v-col class="py-0 my-0" cols="12" sm="4">
                          <v-text-field
                            hide-details
                            label="公費資格內容"
                            v-model="list.publicexpenses_content"
                          ></v-text-field>
                        </v-col>
                        <v-col class="py-0 my-0" cols="12" sm="2">
                          <v-checkbox
                            class="py-0 my-0"
                            label="DNR"
                            v-model="list.dnr"
                            :value="true"
                          ></v-checkbox>
                        </v-col>
                        <v-col class="py-0 my-0" cols="12" sm="4">
                          <v-text-field hide-details label="DNR內容" v-model="list.dnr_content"></v-text-field>
                        </v-col>
                        <v-col class="py-0 my-0" cols="12" sm="3">
                          <v-text-field hide-details label="入住日期" v-model="list.in_date"></v-text-field>
                        </v-col>
                        <v-col class="py-0 my-0" cols="12" sm="3">
                          <v-text-field hide-details label="退住日期" v-model="list.out_date"></v-text-field>
                        </v-col>
                        <v-col class="py-0 my-0" cols="12" sm="3">
                          <v-text-field hide-details label="出生日期" v-model="list.birthday"></v-text-field>
                        </v-col>
                        <v-col class="py-0 my-0" cols="12" sm="3">
                          <v-text-field
                            hide-details
                            label="身分證字號"
                            v-model="list.id_num"
                            counter="10"
                          ></v-text-field>
                        </v-col>
                        <v-col class="py-0 my-0" cols="12" sm="3">
                          <v-text-field hide-details label="入住來源" v-model="list.source"></v-text-field>
                        </v-col>
                        <v-col class="py-0 my-0" cols="12" sm="3">
                          <v-text-field
                            hide-details
                            label="入住來源-其他內容"
                            v-model="list.source_content"
                          ></v-text-field>
                        </v-col>
                        <v-col class="py-0 my-0" cols="12" sm="3">
                          <v-text-field hide-details label="入院方式" v-model="list.enterfn"></v-text-field>
                        </v-col>
                        <v-col class="py-0 my-0" cols="12" sm="3">
                          <v-text-field
                            hide-details
                            label="入院方式-其他內容"
                            v-model="list.enterfn_content"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12">
                          障礙資料:
                          <v-chip
                            class="ma-2"
                            color="primary"
                            v-for="(oitem,index) in obstacleArr"
                            :key="index"
                            outlined
                          >
                            {{oitem.kind}} - {{oitem.lv}} - {{oitem.date}}
                            <!-- <v-icon color="red" @click="delObstacle(index)">mdi-close</v-icon> -->
                          </v-chip>
                        </v-col>
                      </v-row>
                    </td>
                    <td width="20%">
                      <!-- <v-img
                      :src="this.$store.state.base_url+'upload/thumb/'+list.pic_url"
                      :lazy-src="this.$store.state.base_url+'upload/thumb/lazypic.jpg'"
                      aspect-ratio="0.9"
                      class="grey lighten-2 d-flex align-end"
                      ></v-img>-->
                    </td>
                  </tr>
                </table>
                <v-row>
                  <v-col class="py-0 my-0" cols="12" sm="9">
                    <header>語言</header>
                    <v-row justify="space-around">
                      <v-checkbox
                        class="py-0 my-0"
                        label="國語"
                        v-model="list.language1"
                        value="true"
                      ></v-checkbox>
                      <v-checkbox
                        class="py-0 my-0"
                        label="台語"
                        v-model="list.language2"
                        value="true"
                      ></v-checkbox>
                      <v-checkbox
                        class="py-0 my-0"
                        label="客家語"
                        v-model="list.language3"
                        value="true"
                      ></v-checkbox>
                      <v-checkbox
                        class="py-0 my-0"
                        label="筆談"
                        v-model="list.language4"
                        value="true"
                      ></v-checkbox>
                      <v-checkbox
                        class="py-0 my-0"
                        label="圖片"
                        v-model="list.language5"
                        value="true"
                      ></v-checkbox>
                      <v-checkbox
                        class="py-0 my-0"
                        label="手勢"
                        v-model="list.language6"
                        value="true"
                      ></v-checkbox>
                      <v-checkbox
                        class="py-0 my-0"
                        label="其他"
                        v-model="list.language7"
                        value="true"
                      ></v-checkbox>
                    </v-row>
                  </v-col>
                  <v-col class="py-0 my-0" cols="12" sm="3">
                    <v-text-field hide-details label="其他內容" v-model="list.language8"></v-text-field>
                  </v-col>
                  <v-col class="py-0 my-0" cols="12" sm="9">
                    <header>婚姻狀況</header>
                    <v-row justify="space-around">
                      <v-checkbox class="py-0 my-0" label="未婚" v-model="list.marry1" value="true"></v-checkbox>
                      <v-checkbox class="py-0 my-0" label="已婚" v-model="list.marry2" value="true"></v-checkbox>
                      <v-checkbox class="py-0 my-0" label="離婚" v-model="list.marry3" value="true"></v-checkbox>
                      <v-checkbox class="py-0 my-0" label="喪偶" v-model="list.marry4" value="true"></v-checkbox>
                      <v-checkbox class="py-0 my-0" label="分居" v-model="list.marry5" value="true"></v-checkbox>
                      <v-checkbox class="py-0 my-0" label="其他" v-model="list.marry6" value="true"></v-checkbox>
                    </v-row>
                  </v-col>
                  <v-col class="py-0 my-0" cols="12" sm="3">
                    <v-text-field hide-details label="其他內容" v-model="list.marry7"></v-text-field>
                  </v-col>
                  <v-col class="py-0 my-0" cols="12" sm="3">
                    <v-text-field hide-details label="宗教信仰" v-model="list.faith"></v-text-field>
                  </v-col>
                  <v-col class="py-0 my-0" cols="12" sm="3">
                    <v-text-field hide-details label="身高(公分)" v-model="list.height"></v-text-field>
                  </v-col>
                  <v-col class="py-0 my-0" cols="12" sm="3">
                    <v-text-field hide-details label="體重(公斤)" v-model="list.weight"></v-text-field>
                  </v-col>
                  <v-col class="py-0 my-0" cols="12" sm="3">
                    <v-text-field hide-details label="平日就醫地點" v-model="list.usual_hospital"></v-text-field>
                  </v-col>
                </v-row>
              </v-alert>

              <v-alert color="primary" border="left" outlined>
                <v-sheet color="blue dark-1" class="pa-2 white--text" elevation="4">
                  <h2>連絡人資料</h2>
                </v-sheet>
                <v-row>
                  <v-col class="py-0 my-0" cols="12" v-for="(cman,index) in list.cman" :key="index">
                    <v-row>
                      <v-col class="py-0 my-0" cols="12" sm="2">
                        <v-text-field
                          hide-details
                          :label="'緊急連結人'+index"
                          v-model="list.cman[index]"
                        ></v-text-field>
                      </v-col>
                      <v-col class="py-0 my-0" cols="12" sm="2">
                        <v-text-field
                          hide-details
                          :label="'與住民之關係'+index"
                          v-model="list.crelationship[index]"
                        ></v-text-field>
                      </v-col>
                      <v-col class="py-0 my-0" cols="12" sm="2">
                        <v-text-field hide-details :label="'電話'+index" v-model="list.cphone[index]"></v-text-field>
                      </v-col>
                      <v-col class="py-0 my-0" cols="12" sm="6">
                        <v-text-field
                          hide-details
                          :label="'地址'+index"
                          v-model="list.caddress[index]"
                        ></v-text-field>
                      </v-col>
                      <v-col class="py-0 my-0" cols="12" sm="12">
                        <v-text-field hide-details :label="'備註'+index" v-model="list.cnote[index]"></v-text-field>
                      </v-col>
                    </v-row>
                  </v-col>
                </v-row>
              </v-alert>

              <v-alert color="primary" border="left" outlined>
                <v-sheet color="blue dark-1" class="pa-2 white--text" elevation="4">
                  <h2>社經地位評估</h2>
                </v-sheet>

                <v-row>
                  <v-col class="py-0 my-0" cols="12">
                    <header>身分</header>
                    <v-row>
                      <v-col class="py-0 my-0" cols="12">
                        <v-row justify="space-around">
                          <v-checkbox
                            class="py-0 my-0"
                            label="一般"
                            v-model="NursingassessmentItems.identity1"
                            value="true"
                          ></v-checkbox>
                          <v-checkbox
                            class="py-0 my-0"
                            label="中低收入戶"
                            v-model="NursingassessmentItems.identity2"
                            value="true"
                          ></v-checkbox>
                          <v-checkbox
                            class="py-0 my-0"
                            label="低收入戶(一款)"
                            v-model="NursingassessmentItems.identity3"
                            value="true"
                          ></v-checkbox>
                          <v-checkbox
                            class="py-0 my-0"
                            label="低收入戶(二款)"
                            v-model="NursingassessmentItems.identity4"
                            value="true"
                          ></v-checkbox>
                          <v-checkbox
                            class="py-0 my-0"
                            label="低收入戶(三款)"
                            v-model="NursingassessmentItems.identity5"
                            value="true"
                          ></v-checkbox>
                          <v-checkbox
                            class="py-0 my-0"
                            label="原住民"
                            v-model="NursingassessmentItems.identity6"
                            value="true"
                          ></v-checkbox>
                          <v-checkbox
                            class="py-0 my-0"
                            label="榮民(榮眷)"
                            v-model="NursingassessmentItems.identity7"
                            value="true"
                          ></v-checkbox>
                          <v-checkbox
                            class="py-0 my-0"
                            label="其他"
                            v-model="NursingassessmentItems.identity8"
                            value="true"
                          ></v-checkbox>
                        </v-row>
                      </v-col>
                      <v-col class="py-0 my-0" cols="12">
                        <v-text-field
                          hide-details
                          label="其他內容"
                          v-model="NursingassessmentItems.identity9"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                  </v-col>
                </v-row>

                <v-row>
                  <v-col class="py-0 my-0" cols="12">
                    <header>教育</header>
                    <v-row justify="space-around">
                      <v-checkbox
                        class="py-0 my-0"
                        label="不識字"
                        v-model="NursingassessmentItems.education1"
                        value="true"
                      ></v-checkbox>
                      <v-checkbox
                        class="py-0 my-0"
                        label="國小以下"
                        v-model="NursingassessmentItems.education2"
                        value="true"
                      ></v-checkbox>
                      <v-checkbox
                        class="py-0 my-0"
                        label="小學"
                        v-model="NursingassessmentItems.education3"
                        value="true"
                      ></v-checkbox>
                      <v-checkbox
                        class="py-0 my-0"
                        label="國中"
                        v-model="NursingassessmentItems.education4"
                        value="true"
                      ></v-checkbox>
                      <v-checkbox
                        class="py-0 my-0"
                        label="高中(職)"
                        v-model="NursingassessmentItems.education5"
                        value="true"
                      ></v-checkbox>
                      <v-checkbox
                        class="py-0 my-0"
                        label="大學/大專"
                        v-model="NursingassessmentItems.education6"
                        value="true"
                      ></v-checkbox>
                      <v-checkbox
                        class="py-0 my-0"
                        label="大學以上"
                        v-model="NursingassessmentItems.education7"
                        value="true"
                      ></v-checkbox>
                      <v-checkbox
                        class="py-0 my-0"
                        label="不確定"
                        v-model="NursingassessmentItems.education8"
                        value="true"
                      ></v-checkbox>
                    </v-row>
                  </v-col>
                </v-row>

                <v-row class="align-center">
                  <v-col class="py-0 my-0" cols="12">
                    <header>職業</header>
                    <v-row justify="space-around">
                      <v-checkbox
                        class="py-0 my-0"
                        label="公"
                        v-model="NursingassessmentItems.occupation1"
                        value="true"
                      ></v-checkbox>
                      <v-checkbox
                        class="py-0 my-0"
                        label="商"
                        v-model="NursingassessmentItems.occupation2"
                        value="true"
                      ></v-checkbox>
                      <v-checkbox
                        class="py-0 my-0"
                        label="工"
                        v-model="NursingassessmentItems.occupation3"
                        value="true"
                      ></v-checkbox>
                      <v-checkbox
                        class="py-0 my-0"
                        label="教"
                        v-model="NursingassessmentItems.occupation4"
                        value="true"
                      ></v-checkbox>
                      <v-checkbox
                        class="py-0 my-0"
                        label="農"
                        v-model="NursingassessmentItems.occupation5"
                        value="true"
                      ></v-checkbox>
                      <v-checkbox
                        class="py-0 my-0"
                        label="自由業"
                        v-model="NursingassessmentItems.occupation6"
                        value="true"
                      ></v-checkbox>
                      <v-checkbox
                        class="py-0 my-0"
                        label="其他"
                        v-model="NursingassessmentItems.occupation7"
                        value="true"
                      ></v-checkbox>
                    </v-row>
                  </v-col>
                </v-row>
              </v-alert>

              <!-- page1 end -->
              <p style="page-break-after:always"></p>
              <!-- page2 start-->

              <v-alert color="primary" border="left" outlined>
                <v-sheet color="blue dark-1" class="pa-2 white--text" elevation="4">
                  <h2>個人病史</h2>
                </v-sheet>

                <v-row>
                  <v-col class="py-0 my-0" cols="12">
                    <header>入住診斷</header>
                  </v-col>
                  <v-col
                    cols="2"
                    v-for="(item,index) in list.diagnosis"
                    :key="index"
                    class="ma-0 py-0"
                  >
                    <div class="d-flex align-center">
                      <v-text-field
                        hide-details
                        :label="'疾病診斷'+index"
                        v-model="list.diagnosis[index]"
                      ></v-text-field>
                    </div>
                  </v-col>

                  <v-col class="py-0 my-0" cols="12">
                    <v-divider></v-divider>
                  </v-col>

                  <v-col class="py-0 my-0" cols="12">
                    <header>過去病史</header>
                    <v-row>
                      <v-col class="py-0 my-0" cols="12">
                        <h5>1.曾患疾病</h5>
                        <v-row>
                          <v-col class="py-0 my-0" cols="12">
                            <v-text-field hide-details label="曾患疾病" v-model="list.hadadisease"></v-text-field>
                          </v-col>
                        </v-row>

                        <v-row>
                          <v-col class="py-0 my-0" cols="12" sm="1">
                            <v-text-field
                              hide-details
                              label="2.住院次數"
                              v-model="list.behospitalized1"
                            ></v-text-field>
                          </v-col>
                          <v-col class="py-0 my-0" cols="12" sm="1">
                            <v-text-field hide-details label="次" v-model="list.behospitalized2"></v-text-field>
                          </v-col>
                          <v-col class="py-0 my-0" cols="12" sm="2">
                            <v-text-field hide-details label="原因" v-model="list.behospitalized3"></v-text-field>
                          </v-col>
                          <v-col class="py-0 my-0" cols="12" sm="2">
                            <v-text-field hide-details label="住院地點" v-model="list.behospitalized4"></v-text-field>
                          </v-col>
                          <v-col class="py-0 my-0" cols="12" sm="1">
                            <v-text-field
                              hide-details
                              label="3.手術情形"
                              v-model="list.surgerysituation1"
                            ></v-text-field>
                          </v-col>
                          <v-col class="py-0 my-0" cols="12" sm="1">
                            <v-text-field hide-details label="次" v-model="list.surgerysituation2"></v-text-field>
                          </v-col>
                          <v-col class="py-0 my-0" cols="12" sm="2">
                            <v-text-field hide-details label="原因" v-model="list.surgerysituation3"></v-text-field>
                          </v-col>
                          <v-col class="py-0 my-0" cols="12" sm="2">
                            <v-text-field
                              hide-details
                              label="手術地點"
                              v-model="list.surgerysituation4"
                            ></v-text-field>
                          </v-col>

                          <v-col class="py-0 my-0" cols="12" sm="2">
                            <v-text-field
                              hide-details
                              label="4.輸血經驗"
                              v-model="list.bloodtransfusion"
                            ></v-text-field>
                          </v-col>
                          <v-col class="py-0 my-0" cols="12" sm="2">
                            <h5>不良反應</h5>
                            <v-row justify="space-around">
                              <v-checkbox
                                class="py-0 my-0"
                                label="無"
                                v-model="list.adversereactions1"
                                value="true"
                              ></v-checkbox>
                              <v-checkbox
                                class="py-0 my-0"
                                label="有"
                                v-model="list.adversereactions2"
                                value="true"
                              ></v-checkbox>
                            </v-row>
                          </v-col>
                          <v-col class="py-0 my-0" cols="12" sm="1">
                            <v-text-field hide-details label="5.服藥" v-model="list.takingmedicine1"></v-text-field>
                          </v-col>
                          <v-col class="py-0 my-0" cols="12" sm="3">
                            <v-text-field
                              hide-details
                              label="藥物名稱及使用情形"
                              v-model="list.takingmedicine2"
                            ></v-text-field>
                          </v-col>
                          <v-col class="py-0 my-0" cols="12" sm="1">
                            <v-text-field
                              hide-details
                              label="6.先天疾病"
                              v-model="list.congenitaldisease1"
                            ></v-text-field>
                          </v-col>
                          <v-col class="py-0 my-0" cols="12" sm="3">
                            <v-text-field
                              hide-details
                              label="疾病名稱"
                              v-model="list.congenitaldisease2"
                            ></v-text-field>
                          </v-col>
                        </v-row>
                      </v-col>
                    </v-row>
                  </v-col>

                  <v-col class="py-0 my-0" cols="12">
                    <v-divider></v-divider>
                  </v-col>

                  <v-col class="py-0 my-0" cols="12">
                    <header>過敏史</header>
                    <v-row>
                      <v-col class="py-0 my-0" cols="12" sm="2">
                        <v-text-field hide-details label="1.藥物" v-model="list.allergyhistory1"></v-text-field>
                      </v-col>
                      <v-col class="py-0 my-0" cols="12" sm="3">
                        <v-text-field hide-details label="藥名" v-model="list.allergyhistory2"></v-text-field>
                      </v-col>
                      <v-col class="py-0 my-0" cols="12" sm="2">
                        <v-text-field hide-details label="2.食物" v-model="list.allergyhistory3"></v-text-field>
                      </v-col>
                      <v-col class="py-0 my-0" cols="12" sm="3">
                        <v-text-field hide-details label="食物名" v-model="list.allergyhistory4"></v-text-field>
                      </v-col>
                      <v-col class="py-0 my-0" cols="12" sm="2">
                        <v-text-field hide-details label="3.其他" v-model="list.allergyhistory5"></v-text-field>
                      </v-col>
                    </v-row>
                  </v-col>

                  <v-col class="py-0 my-0" cols="12">
                    <v-divider></v-divider>
                  </v-col>

                  <v-col class="py-0 my-0" cols="12">
                    <header>輔助器材</header>
                    <v-row justify="space-around" class="mx-2">
                      <v-checkbox
                        class="py-0 my-0"
                        label="無"
                        v-model="list.auxiliaryequipment1"
                        value="true"
                      ></v-checkbox>
                      <v-checkbox
                        class="py-0 my-0"
                        label="助聽器"
                        v-model="list.auxiliaryequipment2"
                        value="true"
                      ></v-checkbox>
                      <v-checkbox
                        class="py-0 my-0"
                        label="活動假牙"
                        v-model="list.auxiliaryequipment3"
                        value="true"
                      ></v-checkbox>
                      <v-checkbox
                        class="py-0 my-0"
                        label="義眼"
                        v-model="list.auxiliaryequipment4"
                        value="true"
                      ></v-checkbox>
                      <v-checkbox
                        class="py-0 my-0"
                        label="眼鏡"
